type UnionToIntersection<U> = (U extends any ? (k: U) => void : never) extends (k: infer I) => void
  ? I
  : never;

/**
 * pushProps
 * @param target
 * @param key
 * @param value
 * @returns
 */
function pushProp(target: { [key: string]: any }, key: string, value: any): void {
  if (key === 'class') {
    const oldClass = target['class'];
    target['class'] = oldClass ? [oldClass, value].join(' ').trim() : value;
  } else if (key === 'style') {
    if (typeof value == 'object') {
      target['style'] = { ...target['style'], ...value };
    } else {
      throw new Error(
        `Unable to merge prop '${key}'. ` + `Only support 'style' objects not 'style' strings`
      );
    }
  } else if (typeof value === 'function') {
    const oldFn = target[key] as Function | undefined;
    target[key] = oldFn
      ? (...args: any[]) => {
          oldFn(...args);
          (value as Function)(...args);
        }
      : value;
  } else if (
    // skip undefined values
    value === undefined ||
    // skip if same
    (typeof value !== 'object' && value === target[key])
  ) {
    return;
  } else if (!(key in target)) {
    target[key] = value;
  } else {
    // TODO: Could just let last value win, ie:
    throw new Error(
      `Unable to merge prop '${key}'. ` + `Only support 'className', 'style', and event handlers`
      // Merging other allProps seems troublesome
    );
  }
}

/**
 * Merges allProps together:
 *  - duplicate className and class allProps concatenated
 *  - duplicate style allProps merged - note that only style objects supported at this point
 *  - duplicate functions chained
 * @param allProps Props to merge together.
 */
export function mergeProps<T extends {}[]>(
  ...allProps: T
): {
  [K in keyof UnionToIntersection<T[number]>]: K extends 'class'
    ? string
    : K extends 'style'
    ? UnionToIntersection<T[number]>[K]
    : Exclude<Extract<T[number], { [Q in K]: unknown }>[K], undefined>;
} {
  if (allProps.length === 1) {
    return allProps[0] as any;
  }

  return allProps.reduce((merged, props: any) => {
    for (const key in props) {
      pushProp(merged, key, props[key]);
    }
    return merged;
  }, {}) as any;
}
